.App{
  font-size: large;
  background-color: rgb(244, 251, 243);
  min-height: 1000px;
}
.Article{
  border-radius:30px;
  background-color: white;
}

@keyframes rotate {
  0%{
    taransform: rotate(0deg);
  }
  100%{
    taransform: rotate(180deg)
  }
}

.SlideUp{
  animation: rotate 1s;
}
.SlideDown{
  animation:rotate 1s alternate;
}

.center{
  justify-content: center;
  text-align: center;
}
.left{float: left;}
.right{float: right;}
.end{
  display: flex;
  justify-content: flex-end;
  vertical-align: baseline;
}
.MyCard{
  border-top:1px solid gray;
  border-bottom:1px solid gray;
  padding: 2em;
}
.NavbarLink{
  justify-content: center;
  color:rgba(255, 251, 251, 0.377);
  text-decoration: none;
  transition: 1s all 0s ease;
}
.NavbarLink:hover{
  color:white;
  text-decoration: none;
  transition: 1s all 0s ease;
}
.yCenter{vertical-align: middle}
.mySuccess{
  background-color: rgba(9, 185, 9, 0.623);
}
.myDanger{
  background-color: rgba(235, 42, 42, 0.685);
}
.myWarning{
  background-color: rgba(231, 191, 14, 0.712);
}
